.header{
  width: 100%;
  height: 10vh;
  border: 1px solid black;
  box-shadow: 1px 1px 1px 1px;
  display: flex;
  justify-content: space-around;
}

.header a{
  width: 100px;
  border: 1px solid black;
  cursor: pointer;
  text-decoration: none;
  display: flex;
  align-items: center;
  justify-content: center;
}