.body{
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 20px ;
  border-bottom: 3px solid orange;
}

.body img{
  width: 50%;
}

.body button{
  background-color: blue;
  color: white;
  font-size: larger;
  font-weight: bold;
  border: none;
  width: 200px;
  height: 100px;
}

@media screen and (max-width: 700px) {
  .body{
    display: grid;
  }
}